.toggle {
  float: left;
  position: relative;
  width: 44px;
  height: 44px;
  padding: 0;
  font-size: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  z-index: 1000; }
  @media (min-width: 768px) {
    .toggle {
      visibility: hidden; } }
  .toggle::before, .toggle::after {
    position: absolute;
    left: 12px;
    display: block;
    content: "";
    width: 20px;
    height: 2px;
    border: none;
    background: #222;
    transition: all 0.3s ease-out; }
  .toggle::before {
    top: 16px; }
  .toggle::after {
    top: 26px; }
  .toggle--open::before, .toggle--open::after {
    background: #222; }
  .toggle--open::before {
    transform: translateY(5px) rotate(45deg); }
  .toggle--open::after {
    transform: translateY(-5px) rotate(-45deg); }

.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  min-height: 100vh;
  z-index: 0;
  display: none;
